import React, { useState } from "react";
import ExchangeRate from "./ExchangeRate";
import axios from "axios";

const CurrencyConverter = () => {
  const currencies = ["BTC", "ETH", "USD", "XRP", "LTC", "ADA"];

  const [chosenPrimaryCurrency, setChosePrimaryCurrency] = useState("BTC");
  const [chosenSecondayCurrency, setChoseSecondaryCurrency] = useState(null);
  const [inputAmount, setInputAmount] = useState(1);

  const [exchangeRate, setExchangeRate] = useState(0);

  const [result, setResult] = useState(0);

  const convert = () => {
    const options = {
      method: "GET",
      url: "http://alpha-vantage.p.rapidapi.com/query",
      params: {
        from_currency: chosenPrimaryCurrency,
        function: "CURRENCY_EXCHANGE_RATE",
        to_currency: chosenSecondayCurrency,
      },
      headers: {
        "x-rapidapi-host": "alpha-vantage.p.rapidapi.com",
        "x-rapidapi-key": process.env.REACT_APP_RAPID_API_KEY,
      },
    };

    axios
      .request(options)
      .then(function (response) {
       
        setExchangeRate(
          response.data["Realtime Currency Exchange Rate"]["5. Exchange Rate"]
        );
        setResult(
          response.data["Realtime Currency Exchange Rate"]["5. Exchange Rate"] *
            inputAmount
        );
      })
      .catch(function (error) {
        console.error(error);
      });
  };

  return (
    <div className="currencyConverter">
      <h1>currency converter</h1>
      <div className="inputBox">
        <table>
          <tbody>
            <tr>
              <td>Primary Currency</td>
              <td>
                <input
                  type="number"
                  name="currency-amount-one"
                  value={inputAmount}
                  onChange={(e) => setInputAmount(e.target.value)}
                />
              </td>
              <td>
                <select
                  value={chosenPrimaryCurrency}
                  name="currency-option-one"
                  className="currencyOptions"
                  onChange={(e) => setChosePrimaryCurrency(e.target.value)}
                >
                  {currencies.map((currency, _index) => {
                    return <option>{currency}</option>;
                  })}
                </select>
              </td>
            </tr>
            <tr>
              <td>Secondary Currency</td>
              <td>
                <input
                  type="number"
                  name="currency-amount-two"
                  value={result}
                  disabled={true}
                />
              </td>
              <td>
                <select
                  value={chosenSecondayCurrency}
                  name="currency-option-two"
                  className="currencyOptions"
                  onChange={(e) => setChoseSecondaryCurrency(e.target.value)}
                >
                  {currencies.map((currency, _index) => {
                    return <option>{currency}</option>;
                  })}
                </select>
              </td>
            </tr>
          </tbody>
        </table>

        <button className="convertBtn" onClick={convert}>
          Convert
        </button>
      </div>
      <ExchangeRate className = "exchangeRate"
        exchangeRate={exchangeRate}
        chosenPrimaryCurrency={chosenPrimaryCurrency}
        chosenSecondayCurrency={chosenSecondayCurrency}
      />
    </div>
  );
};

export default CurrencyConverter;
