import React from "react";

const ExchangeRate = ({
  exchangeRate,
  chosenPrimaryCurrency,
  chosenSecondayCurrency,
}) => {
  return (
    <div className="exchangeRate">
      <h3>Exchange Rate</h3>
      <h1>{exchangeRate}</h1>
      <p>
        From {chosenPrimaryCurrency} to {chosenSecondayCurrency}
      </p>
    </div>
  );
};

export default ExchangeRate;
